body{
    background-color: rgb(240, 247, 247); 
}

#signin{
    width:400px;
    height:400px;    
    margin:auto;    
}

.card-header{
    background-color: grey;
    color:white;
}
nav{
    height:75px;
    background-color:grey;
    margin-bottom:1rem;
}
.card-body{
    color:black;
}

.gradeInput{
    width: 50px;
    background-color:white;
}
.card,.addSubject{
    box-shadow: 4px 2px 25px #888888;
}
.addForm,.addSubject{
}

.addSubject{
    background-color:white;
    border-radius: 3px;
}

.input-label,.input-block{
    width:150px !important;    
}

.editIcon{
    font-size:1.25rem;
    cursor: pointer;
    color:white;
}

.top-stripe{
    height:10px;
    border-radius: 3px 3px 0px 0px;
}

.fa-trash-alt,.fa-eye,.fa-info-circle{
    margin:.5rem .2rem;
    font-size:1rem;
    color:red;
    cursor: pointer;
}
.fa-eye,.fa-info-circle{
    color:grey;
}
.fa-eye:hover{
    color:blue;
    font-size:1.125rem;
}

.hidden{
    display:none
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

